import React, { useEffect, useRef, useState } from "react";
import Start from "./Start/Start";
import TaskEntry from "./TaskEntry/TaskEntry";
function App() {
    const name = useRef();
    const timeToRemind = useRef();
    const [shiftPage, setShiftPage] = useState(false);
    const [startEntry, setStartEntry] = useState(false);
    const [username, setUsername] = useState("");
    useEffect(() => {
        chrome.storage.local.get((res) => {
            if (res.username && res.timeToRemind) {
                setUsername(res.username);
                setStartEntry(true);
            }
        });
    });
    const onNameSave = (e) => {
        e.preventDefault();
        name.current.value = name.current.value.trim().toUpperCase();
        if (name.current.value) {
            chrome.storage.local.set({
                username: name.current.value,
            }, () => {
                setShiftPage(true);
            });
        }
    };
    const onTimeSave = (e) => {
        e.preventDefault();
        if (timeToRemind.current.value) {
            chrome.storage.local.set({
                timeToRemind: timeToRemind.current.value,
            }, () => {
                chrome.runtime.sendMessage({ time: timeToRemind.current.value });
                setStartEntry(true);
            });
        }
    };
    return (React.createElement("div", null, !startEntry ? (!shiftPage ? (React.createElement(Start, { heading: "Let's get started!", subHeading: "Hey Buddy, what should I call you?", inputRef: name, inputType: "text", onSave: onNameSave })) : (React.createElement(Start, { heading: `Hi ${name.current.value}`, subHeading: "When you want me to remind?", inputRef: timeToRemind, inputType: "time", onSave: onTimeSave }))) : (React.createElement(TaskEntry, { username: username }))));
}
export default App;
